import React, { useState } from "react";
import { Table, Radio, Divider } from "antd";

const TabTable = ({
  data,
  col,
  vSum,
  a,
  tabTrackVal,
  setTabTrackVal,
  setDesign,
  tabKey,
  setMust,
  education,
  setEducation,
}) => {
  const [selectionType, setSelectionType] = useState("checkbox");

  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      let grade = 0;
      let a = 0;
      let b = 0;
      let c = 0;
      let designCredits = 0;
      let must = 0;
      let aa = 0;
      let bb = 0;
      let cc = 0;
      let dd = 0;
      let ee = 0;
      let ff = 0;

      for (let i = 0; i < selectedRows.length; i++) {
        grade = grade + selectedRows[i].grade;
        if (tabKey === 1) {
          selectedRows[i].track === "시스템분석/경영공학" && (a = a + 1);
          selectedRows[i].track === "품질/정보" && (b = b + 1);
          selectedRows[i].track === "생산/물류/인간공학" && (c = c + 1);
          selectedRows[i].DesignCredits &&
            (designCredits =
              designCredits + Number(selectedRows[i].DesignCredits));
          selectedRows[i].must && (must = must + 1);
        }
        if (tabKey === 3) {
          (selectedRows[i].subject === "진" ||
            selectedRows[i].subject === "성" ||
            selectedRows[i].subject === "애") &&
            (aa = aa + 1);
          selectedRows[i].subject === "사고와표현" && (bb = bb + 1);
          selectedRows[i].subject.indexOf("입문") > -1 && (cc = cc + 1);
          selectedRows[i].subject.indexOf("회화") > -1 && (dd = dd + 1);
          selectedRows[i].subject === "공학윤리" && (ee = ee + 1);
          selectedRows[i].subject.indexOf("특허와") > -1 && (ff = ff + 1);
        }
      }
      vSum(grade);
      if (tabKey === 1) {
        setDesign(designCredits);
        setTabTrackVal({ ...tabTrackVal, a: a, b: b, c: c });
        setMust(must);
      }
      if (tabKey === 3) {
        setEducation({
          ...education,
          a: aa,
          b: bb,
          c: cc,
          d: dd,
          e: ee,
          f: ff,
        });
      }
    },

    getCheckboxProps: (record) => ({
      disabled: a ? record.subject : "",
    }),
  };
  return (
    <div style={{ width: "90%", marginLeft: "70px" }}>
      {/*       <Radio.Group
        onChange={({ target: { value } }) => {
          setSelectionType(value);
        }}
        value={selectionType}
      >
        <Radio value="checkbox">Checkbox</Radio>
        <Radio value="radio">radio</Radio>
      </Radio.Group> */}

      <Divider />

      <Table
        rowSelection={{
          type: selectionType,
          ...rowSelection,
        }}
        columns={col}
        dataSource={data}
        scroll={{ y: 240 }}
        pagination={false}
      />
    </div>
  );
};

export default TabTable;
